<template>
  <section id="projects" class="projects">
    <div class="container">
      <h2 class="section-title">Mes Projets</h2>
      <p class="section-subtitle">Découvrez mes réalisations et projets personnels</p>
      
      <div class="projects-grid">
        <div 
          v-for="project in projects" 
          :key="project.id" 
          class="project-card"
        >
          <div class="project-image">
            <img 
              :src="project.image" 
              :alt="project.title"
              v-if="project.image"
              @error="handleImageError"
            />
            <div v-else class="image-placeholder">
              <i class="fas fa-laptop-code"></i>
              <span>{{ project.title }}</span>
            </div>
            <div class="project-overlay">
              <div class="project-links">
                <a 
                  v-if="project.demoUrl" 
                  :href="project.demoUrl" 
                  target="_blank" 
                  class="project-link demo"
                >
                  <i class="fas fa-external-link-alt"></i>
                  Voir le projet
                </a>
                <a 
                  :href="project.githubUrl" 
                  target="_blank" 
                  class="project-link github"
                >
                  <i class="fab fa-github"></i>
                  Code source
                </a>
              </div>
            </div>
          </div>
          
          <div class="project-content">
            <div class="project-category">{{ project.category }}</div>
            <h3 class="project-title">{{ project.title }}</h3>
            <p class="project-description">{{ project.description }}</p>
            
            <div class="project-technologies">
              <span 
                v-for="tech in project.technologies" 
                :key="tech" 
                class="tech-tag"
              >
                {{ tech }}
              </span>
            </div>
            
            <div class="project-actions">
              <button 
                @click="openProjectModal(project)"
                class="btn-more"
              >
                <i class="fas fa-info-circle"></i>
                Plus de détails
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal -->
    <div v-if="selectedProject" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <button class="modal-close" @click="closeModal">
          <i class="fas fa-times"></i>
        </button>
        
        <div class="modal-body">
          <div class="modal-image">
            <img 
              :src="selectedProject.image" 
              :alt="selectedProject.title" 
              v-if="selectedProject.image"
            />
            <div v-else class="image-placeholder">
              <i class="fas fa-laptop-code"></i>
              <span>{{ selectedProject.title }}</span>
            </div>
          </div>
          
          <div class="modal-details">
            <div class="modal-category">{{ selectedProject.category }}</div>
            <h2 class="modal-title">{{ selectedProject.title }}</h2>
            <p class="modal-full-description">{{ selectedProject.fullDescription }}</p>
            
            <div class="modal-technologies">
              <h4>Technologies utilisées :</h4>
              <div class="tech-list">
                <span 
                  v-for="tech in selectedProject.technologies" 
