              <a 
                v-if="selectedProject.demoUrl" 
                :href="selectedProject.demoUrl" 
                target="_blank" 
                class="btn btn-primary"
              >
                <i class="fas fa-external-link-alt"></i>
                Voir le projet
              </a>
              <a 
                :href="selectedProject.githubUrl" 
                target="_blank" 
                class="btn btn-secondary"
              >
                <i class="fab fa-github"></i>
                Code source
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import ocpImage from '@/assets/images/volunteering/ocp.jpeg'
import portfo from '@/assets/images/volunteering/portfo.png'
export default {
  name: 'Projects',
  data() {
    return {
      selectedProject: null,
      projects: [
        {
          id: 1,
          title: 'Application de Gestion de Stock OCP',
          description: 'Application web complète pour la gestion du stock avec interface intuitive.',
          fullDescription: 'Application web développée avec Django permettant la gestion complète du stock : ajout, modification, suppression d\'articles, gestion des catégories, suivi des mouvements de stock, et génération de rapports.',
          image: ocpImage, // Même format que profile.jpeg
          technologies: ['Django', 'Python', 'HTML5', 'CSS3', 'JavaScript', 'SQLite'],
          category: 'Full Stack',
          demoUrl: null,
          githubUrl: 'https://github.com/sofiastron/gestion_parc_ocp.git'
        },
        {
          id: 2,
          title: 'Portfolio Personnel',
          description: 'Site web portfolio moderne présentant mes compétences et projets.',
          fullDescription: 'Portfolio développé avec Vue.js 3 présentant mes projets, compétences et expériences avec un design moderne et responsive.',
          image: portfo, 
          technologies: ['Vue.js', 'JavaScript', 'CSS3', 'HTML5'],
          category: 'Frontend',
          demoUrl: 'https://sofiastrom.github.io',
          githubUrl:null
        }
      ]
    }
  },
  methods: {
    handleImageError(event) {
      console.log('Image non trouvée');
      // Remplacer par un placeholder
      const parent = event.target.parentElement;
      parent.innerHTML = `
        <div class="image-placeholder">
          <i class="fas fa-laptop-code"></i>
          <span>Image du projet</span>
        </div>
        ${parent.innerHTML}
      `;
      event.target.remove();
    },
    
    openProjectModal(project) {
      this.selectedProject = project;
      document.body.style.overflow = 'hidden';
    },
    
    closeModal() {
      this.selectedProject = null;
      document.body.style.overflow = 'auto';
    }
  }
}
</script>

