<template>
  <section id="certifications" class="certifications">
    <div class="container">
      <h2 class="section-title">Mes Certifications</h2>
      <p class="section-subtitle">Formations et certifications qui valident mes compétences</p>
      
      <div class="certifications-filters">
        <button 
          v-for="filter in filters" 
          :key="filter"
          :class="['filter-btn', { active: activeFilter === filter }]"
          @click="setFilter(filter)"
        >
          {{ filter }}
        </button>
      </div>

      <div class="certifications-grid">
        <div 
          class="certification-card" 
          v-for="(cert, index) in filteredCertifications" 
          :key="cert.title"
          :class="cert.status"
        >
          <div class="certification-header">
            <div class="certification-badge">
              <i :class="cert.icon"></i>
            </div>
            <div class="certification-info">
              <h3>{{ cert.title }}</h3>
              <p class="cert-issuer">{{ cert.issuer }}</p>
              <p class="cert-date">{{ cert.date }}</p>
            </div>
          </div>
          
          <div class="certification-body">
            <p class="cert-description">{{ cert.description }}</p>
            
            <div class="cert-skills">
              <span 
                v-for="skill in cert.skills" 
                :key="skill" 
                class="skill-tag"
              >
                {{ skill }}
              </span>
            </div>
          </div>

          <div class="certification-footer">
            <div class="cert-status" :class="cert.status">
              <i :class="getStatusIcon(cert.status)"></i>
              {{ getStatusText(cert.status) }}
            </div>
            <div class="cert-actions">
              <a 
                v-if="cert.credentialUrl" 
                :href="cert.credentialUrl" 
                target="_blank" 
                rel="noopener noreferrer"
                class="btn-view"
              >
                Voir le certificat
              </a>
              <button 
                v-if="cert.details" 
                @click="toggleDetails(index)" 
                class="btn-details"
                :aria-expanded="openDetails.includes(index)"
              >
                {{ openDetails.includes(index) ? 'Moins' : 'Plus' }}
              </button>
            </div>
          </div>

          <div 
            v-if="openDetails.includes(index)" 
            class="certification-details"
            :id="`details-${index}`"
          >
            <div class="details-content">
              <h4>Ce que j'ai appris :</h4>
              <ul>
                <li v-for="(detail, detailIndex) in cert.details" :key="detailIndex">
                  {{ detail }}
                </li>
              </ul>
              
              <div v-if="cert.projects" class="related-projects">
                <h4>Projets réalisés :</h4>
                <div class="projects-list">
                  <span 
                    v-for="project in cert.projects" 
                    :key="project" 
                    class="project-tag"
                  >
                    {{ project }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="certifications-stats">
        <div class="stat" v-for="stat in stats" :key="stat.label">
          <div class="stat-number">{{ stat.number }}</div>
          <div class="stat-label">{{ stat.label }}</div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Certifications',
  data() {
    return {
      activeFilter: 'Toutes',
      filters: ['Toutes', 'Développement', 'Cloud', 'Data', 'Intelligence Artificielle', 'En cours'],
      openDetails: [],
      stats: [
        { number: '7', label: 'Certifications' },
        { number: '500h+', label: 'Formation' },
        { number: '10+', label: 'Plateformes' },
        { number: '3', label: 'Niveaux' }
      ],
      certifications: [
        {
          title: 'Data Science and Analytics',
          issuer: 'HP Life',
          date: 'Septembre 2024',
          description: 'Certificat de complétion du cours HP Life en Data Science et Analyse de données.',
          category: 'Data',
          status: 'completed',
          icon: 'fas fa-chart-line',
          skills: ['Data Analysis', 'Python', 'Machine Learning', 'Statistics', 'Visualization'],
          credentialUrl: 'https://drive.google.com/file/d/1cGuA6nn2GodAWOeJPLTPVZOu_c5vu1Mo/view?usp=drivesdk',
          details: [
            'Introduction à la Data Science et à ses applications pratiques',
            'Analyse de données et création d\'indicateurs',
            'Utilisation d\'outils analytiques pour la prise de décision',
            'Exploration des tendances et visualisation des résultats'
          ]
        },
        {
          title: 'Python Essentials 2',
          issuer: 'Cisco Networking Academy',
          date: 'Août 2024',
          description: 'Certificat de réussite au programme Python Essentials 2 de Cisco Networking Academy.',
          category: 'Développement',
          status: 'completed',
          icon: 'fab fa-python',
          skills: ['Python', 'OOP', 'Modules', 'Files', 'Error Handling'],
          credentialUrl: 'https://drive.google.com/file/d/1ep5tQqh6qZL6SjlnAG-D5iwtH5ljmxwo/view?usp=drivesdk',
          details: [
            'Programmation orientée objet en Python',
            'Manipulation des fichiers et gestion des exceptions',
            'Utilisation des modules standards et externes',
            'Création de programmes structurés et modulaires'
          ]
        },
        {
          title: 'JavaScript Essentials 2',
          issuer: 'Cisco Networking Academy',
          date: 'Août 2024',
          description: 'Certificat de réussite au programme JavaScript Essentials 2 de Cisco Networking Academy.',
          category: 'Développement',
          status: 'completed',
          icon: 'fab fa-js-square',
          skills: ['JavaScript', 'DOM', 'Async/Await', 'API Fetch'],
          credentialUrl: 'https://drive.google.com/file/d/1yhZZDNpyLLL9aOvqklpPgmT-l98-3tk6/view?usp=drivesdk',
          details: [
            'Utilisation avancée de JavaScript moderne (ES6+)',
            'Programmation asynchrone avec Promises et Async/Await',
            'Manipulation du DOM et interaction utilisateur',
            'Connexion à des APIs REST et gestion des données'
          ]
        },
        {
          title: 'SQL Basics',
          issuer: 'HackerRank',
          date: 'Juillet 2024',
          description: 'Certificat d\'accomplissement du niveau SQL Basics sur HackerRank.',
          category: 'Data',
          status: 'completed',
          icon: 'fas fa-database',
          skills: ['SQL', 'Select Queries', 'Aggregations', 'Filtering', 'Sorting'],
          credentialUrl: 'https://drive.google.com/file/d/13xzZMfy_59cQfT-uU7zUES1Q_N8Q2x6z/view?usp=drivesdk',
          details: [
            'Écriture de requêtes simples avec SELECT et WHERE',
            'Filtrage, tri et agrégation des données',
            'Utilisation de fonctions SQL de base',
            'Manipulation de tables et jointures simples'
          ]
        },
        {
          title: 'SQL Intermediate',
          issuer: 'HackerRank',
          date: 'Juillet 2024',
          description: 'Certificat d\'accomplissement du niveau SQL Intermediate sur HackerRank.',
          category: 'Data',
          status: 'completed',
          icon: 'fas fa-database',
          skills: ['SQL', 'Joins', 'Subqueries', 'Grouping', 'Optimization'],
          credentialUrl: 'https://drive.google.com/file/d/1-bo1q1WfF-qA6P4eIUs8HomvKt7C1Oiz/view?usp=drivesdk',
          details: [
            'Requêtes SQL complexes avec jointures multiples',
            'Utilisation des sous-requêtes et des vues',
            'Agrégations et regroupements avancés',
            'Optimisation et bonnes pratiques SQL'
          ]
        },
        {
          title: 'Génération d\'IA – Partie 1 : L\'évolution de la recherche en ligne',
          issuer: 'edapp',
          date: 'Octobre 2024',
          description: 'Certificat de complétion du cours "À l\'ère de la Génération d\'IA" — Partie 1 : L\'évolution de la recherche en ligne.',
          category: 'Intelligence Artificielle',
          status: 'completed',
          icon: 'fas fa-robot',
          skills: ['AI Basics', 'Digital Literacy', 'Online Research', 'Information Management'],
          credentialUrl: 'https://drive.google.com/file/d/1FC8zi93z0K3ZKSBzhAvK5XP6h7l44bf5/view?usp=drivesdk',
          details: [
            'Compréhension de la transformation de la recherche à l\'ère de l\'IA',
            'Introduction aux moteurs de recherche intelligents',
            'Bonnes pratiques pour trouver et évaluer l\'information en ligne',
            'Éthique et fiabilité dans la recherche numérique'
          ]
        },
        {
          title: 'Introduction to Artificial Intelligence – Final Exam',
          issuer: 'eddapp',
          date: 'Octobre 2024',
          description: 'Certificat de réussite de l\'examen final du cours "Introduction to Artificial Intelligence".',
          category: 'Intelligence Artificielle',
          status: 'completed',
          icon: 'fas fa-brain',
          skills: ['AI Fundamentals', 'Machine Learning', 'Automation', 'Digital Transformation'],
          credentialUrl: 'https://drive.google.com/file/d/1ohiVXSR7nNGJwnHkjxGWPI86D7qaBGTi/view?usp=drivesdk',
          details: [
            'Notions fondamentales de l\'intelligence artificielle',
            'Applications concrètes de l\'IA dans la productivité et la bureautique',
            'Utilisation d\'outils IA pour Microsoft 365 et l\'automatisation de tâches',
            'Réflexion sur l\'impact de l\'IA sur le travail et l\'apprentissage'
          ]
        }
      ]
    }
  },
  computed: {
    filteredCertifications() {
      if (this.activeFilter === 'Toutes') {
        return this.certifications
      }
      if (this.activeFilter === 'En cours') {
        return this.certifications.filter(cert => cert.status !== 'completed')
      }
      return this.certifications.filter(cert => cert.category === this.activeFilter)
    }
  },
  methods: {
    setFilter(filter) {
      this.activeFilter = filter
    },
    toggleDetails(index) {
      if (this.openDetails.includes(index)) {
        this.openDetails = this.openDetails.filter(i => i !== index)
      } else {
        this.openDetails.push(index)
      }
    },
    getStatusIcon(status) {
      const icons = {
        'completed': 'fas fa-check-circle',
        'in-progress': 'fas fa-spinner',
        'planned': 'fas fa-clock'
      }
      return icons[status] || 'fas fa-certificate'
    },
    getStatusText(status) {
      const texts = {
        'completed': 'Obtenue',
        'in-progress': 'En cours',
        'planned': 'Planifiée'
      }
      return texts[status] || 'Inconnu'
    }
  }
}
</script>

<style scoped>
.certifications {
  background: var(--white);
  padding: 80px 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.section-subtitle {
  text-align: center;
  font-size: 1.2rem;
  color: var(--text-color);
  opacity: 0.8;
  margin-bottom: 3rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.certifications-filters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 3rem;
}

.filter-btn {
  padding: 0.8rem 1.5rem;
  border: 2px solid var(--secondary-color);
  background: transparent;
  color: var(--secondary-color);
  border-radius: 25px;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 500;
  font-size: 0.9rem;
}

.filter-btn.active,
.filter-btn:hover {
  background: var(--secondary-color);
  color: white;
  transform: translateY(-2px);
}

.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.certification-card {
  background: var(--white);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: var(--shadow);
  transition: var(--transition);
  border-left: 4px solid var(--secondary-color);
  position: relative;
  overflow: hidden;
}

.certification-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

.certification-card.completed {
  border-left-color: #27ae60;
}

.certification-card.in-progress {
  border-left-color: #f39c12;
}

.certification-card.planned {
  border-left-color: #95a5a6;
}

.certification-header {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.certification-badge {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, var(--secondary-color), #3498db);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.certification-card.completed .certification-badge {
  background: linear-gradient(135deg, #27ae60, #2ecc71);
}

.certification-card.in-progress .certification-badge {
  background: linear-gradient(135deg, #f39c12, #f1c40f);
}

.certification-card.planned .certification-badge {
  background: linear-gradient(135deg, #95a5a6, #bdc3c7);
}

.certification-info h3 {
  color: var(--primary-color);
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  line-height: 1.3;
}

.cert-issuer {
  color: var(--secondary-color);
  font-weight: 600;
  margin-bottom: 0.3rem;
  font-size: 1rem;
}

.cert-date {
  color: var(--text-color);
  opacity: 0.7;
  font-size: 0.9rem;
}

.certification-body {
  margin-bottom: 1.5rem;
}

.cert-description {
  color: var(--text-color);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

.cert-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.skill-tag {
  background: var(--light-bg);
  color: var(--primary-color);
  padding: 0.4rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: var(--transition);
}

.skill-tag:hover {
  background: var(--secondary-color);
  color: white;
  transform: translateY(-1px);
}

.certification-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.5rem;
  border-top: 1px solid #eee;
}

.cert-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 0.9rem;
}

.cert-status.completed {
  color: #27ae60;
}

.cert-status.in-progress {
  color: #f39c12;
}

.cert-status.planned {
  color: #95a5a6;
}

.cert-actions {
  display: flex;
  gap: 1rem;
}

.btn-view {
  background: var(--secondary-color);
  color: white;
  padding: 0.6rem 1.2rem;
  border-radius: 5px;
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  transition: var(--transition);
  border: none;
  cursor: pointer;
  display: inline-block;
}

.btn-view:hover {
  background: var(--primary-color);
  transform: translateY(-2px);
}

.btn-details {
  background: transparent;
  color: var(--secondary-color);
  padding: 0.6rem 1.2rem;
  border-radius: 5px;
  border: 1px solid var(--secondary-color);
  font-size: 0.9rem;
  font-weight: 500;
  transition: var(--transition);
  cursor: pointer;
}

.btn-details:hover {
  background: var(--secondary-color);
  color: white;
  transform: translateY(-2px);
}

.certification-details {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid #eee;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.details-content h4 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.details-content ul {
  list-style: none;
  padding: 0;
  margin-bottom: 1.5rem;
}

.details-content li {
  padding: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
  color: var(--text-color);
  line-height: 1.5;
}

.details-content li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--secondary-color);
  font-weight: bold;
}

.related-projects h4 {
  margin-bottom: 1rem;
}

.projects-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.project-tag {
  background: linear-gradient(135deg, var(--secondary-color), #3498db);
  color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.certifications-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  padding-top: 3rem;
  border-top: 1px solid #eee;
}

.stat {
  text-align: center;
  padding: 2rem 1rem;
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--secondary-color);
  margin-bottom: 0.5rem;
}

.stat-label {
  color: var(--text-color);
  font-size: 1rem;
  opacity: 0.8;
}

/* Responsive */
@media (max-width: 768px) {
  .certifications {
    padding: 60px 15px;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .certifications-grid {
    grid-template-columns: 1fr;
  }
  
  .certification-card {
    padding: 1.5rem;
  }
  
  .certification-header {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .certification-footer {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .cert-actions {
    justify-content: center;
  }
  
  .certifications-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  
  .stat {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .certifications-filters {
    gap: 0.5rem;
  }
  
  .filter-btn {
    padding: 0.6rem 1rem;
    font-size: 0.8rem;
  }
  
  .certifications-grid {
    grid-template-columns: 1fr;
  }
  
  .certifications-stats {
    grid-template-columns: 1fr;
  }
  
  .cert-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .btn-view,
  .btn-details {
    width: 100%;
    text-align: center;
  }
}
</style>