<template>
  <section id="contact" class="contact">
    <div class="container">
      <div class="contact-header">
        <h2 class="section-title">Contactez-moi</h2>
        <p class="section-subtitle">Discutons de votre prochain projet</p>
      </div>
      
      <div class="contact-content">
        <!-- Côté informations -->
        <div class="contact-info">
          <div class="info-card">
            <div class="card-header">
              <div class="header-icon">
                <i class="fas fa-paper-plane"></i>
              </div>
              <div class="header-text">
                <h3>Travaillons ensemble</h3>
                <p>Parlons de votre vision</p>
              </div>
            </div>
            
            <p class="card-description">
              Vous avez un projet en tête ? N'hésitez pas à me contacter. 
              Je serai ravi de discuter de vos idées et de voir comment je peux vous aider à concrétiser votre vision.
            </p>
            
            <div class="divider"></div>
            
            <div class="contact-methods">
              <div 
                class="contact-method" 
                v-for="method in contactMethods" 
                :key="method.type"
                @mouseenter="hoveredMethod = method.type"
                @mouseleave="hoveredMethod = null"
              >
                <div 
                  class="method-icon"
                  :class="{ 'hovered': hoveredMethod === method.type }"
                  :style="{ backgroundColor: method.color }"
                >
                  <i :class="method.icon"></i>
                </div>
                <div class="method-info">
                  <h4>{{ method.type }}</h4>
                  <p>{{ method.value }}</p>
                  <span class="method-label">Disponible</span>
                </div>
              </div>
            </div>
            
            <div class="divider"></div>
            
            <div class="social-section">
              <h4>Réseaux sociaux</h4>
              <p>Connectons-nous</p>
              <div class="social-links">
                <a 
                  v-for="social in socialLinks" 
                  :key="social.name" 
                  :href="social.url" 
                  target="_blank" 
                  class="social-link"
                  :title="social.name"
                  @mouseenter="hoveredSocial = social.name"
                  @mouseleave="hoveredSocial = null"
                >
                  <div 
                    class="social-icon-wrapper"
                    :style="{ backgroundColor: social.color }"
                  >
                    <i :class="social.icon"></i>
                  </div>
                  <span class="social-name">{{ social.name }}</span>
                </a>
              </div>
            </div>
          </div>
        </div>

        <!-- Côté formulaire -->
        <div class="contact-form-wrapper">
          <form class="contact-form" @submit.prevent="submitForm">
            <div class="form-header">
              <div class="form-icon">
                <i class="fas fa-envelope-open-text"></i>
              </div>
              <div class="form-header-text">
                <h3>Envoyez un message</h3>
                <p>Je vous réponds dans les 24 heures</p>
              </div>
            </div>
            
            <div class="form-grid">
              <div class="form-group" :class="{ 'focused': focusedField === 'name' }">
                <label for="name">Votre nom</label>
                <input 
                  type="text" 
                  id="name"
                  v-model="form.name" 
                  placeholder="John Doe"
                  required
                  @focus="focusedField = 'name'"
                  @blur="focusedField = null"
                >
                <div class="input-icon">
                  <i class="fas fa-user"></i>
                </div>
              </div>
              
              <div class="form-group" :class="{ 'focused': focusedField === 'email' }">
                <label for="email">Votre email</label>
                <input 
                  type="email" 
                  id="email"
                  v-model="form.email" 
                  placeholder="john@example.com"
                  required
                  @focus="focusedField = 'email'"
                  @blur="focusedField = null"
                >
                <div class="input-icon">
                  <i class="fas fa-envelope"></i>
                </div>
              </div>
            </div>
            
            <div class="form-group" :class="{ 'focused': focusedField === 'subject' }">
              <label for="subject">Sujet du message</label>
              <input 
                type="text" 
                id="subject"
                v-model="form.subject" 
                placeholder="Proposition de projet"
                required
                @focus="focusedField = 'subject'"
                @blur="focusedField = null"
              >
              <div class="input-icon">
                <i class="fas fa-tag"></i>
              </div>
            </div>
            
            <div class="form-group" :class="{ 'focused': focusedField === 'message' }">
              <label for="message">Votre message</label>
              <textarea 
                id="message"
                v-model="form.message" 
                placeholder="Décrivez votre projet en détail..."
                rows="6" 
                required
                @focus="focusedField = 'message'"
                @blur="focusedField = null"
              ></textarea>
              <div class="input-icon">
                <i class="fas fa-comment"></i>
              </div>
              <div class="char-count">
                {{ form.message.length }}/500
              </div>
            </div>
            
            <button 
              type="submit" 
              class="btn btn-primary" 
              :disabled="isSubmitting || !isFormValid"
              :class="{ 'submitting': isSubmitting }"
            >
              <span class="btn-content">
                <i class="fas fa-paper-plane btn-icon"></i>
                {{ isSubmitting ? 'Envoi en cours...' : 'Envoyer le message' }}
              </span>
              <div class="btn-loader" v-if="isSubmitting">
                <div class="loader"></div>
              </div>
            </button>
          </form>
        </div>
      </div>
    </div>
    
    <!-- Notification de succès -->
    <div v-if="showSuccess" class="notification success">
      <div class="notification-content">
        <i class="fas fa-check-circle"></i>
        <span>Message envoyé avec succès ! Je vous réponds rapidement.</span>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Contact',
  data() {
    return {
      isSubmitting: false,
      showSuccess: false,
      focusedField: null,
      hoveredMethod: null,
      hoveredSocial: null,
      form: {
        name: '',
        email: '',
        subject: '',
        message: ''
      },
      contactMethods: [
        { 
          type: 'Email', 
          value: 'bouhouchsofia3@gmail.com', 
          icon: 'fas fa-envelope',
          color: '#6b7280'
        },
        { 
          type: 'Téléphone', 
          value: '+212 708977555', 
          icon: 'fas fa-phone',
          color: '#4b5563'
        },
        { 
          type: 'Localisation', 
          value: 'SAFI, MAROC', 
          icon: 'fas fa-map-marker-alt',
          color: '#374151'
        }
      ],
      socialLinks: [
        { 
          name: 'GitHub', 
          url: 'https://github.com/sofiastron', 
          icon: 'fab fa-github',
          color: '#1f2937'
        },
        { 
          name: 'LinkedIn', 
          url: 'https://www.linkedin.com/in/sofia-bouhouch-a5247a299/', 
          icon: 'fab fa-linkedin-in',
          color: '#111827'
        }
      ]
    }
  },
  computed: {
    isFormValid() {
      return (
        this.form.name.trim() !== '' &&
        this.form.email.trim() !== '' &&
        this.form.subject.trim() !== '' &&
        this.form.message.trim() !== '' &&
        this.form.message.length <= 500
      )
    }
  },
  methods: {
    async submitForm() {
  if (!this.isFormValid) return

  this.isSubmitting = true

  try {
    const response = await fetch("https://formspree.io/f/xanlavkn", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: this.form.name,
        email: this.form.email,
        subject: this.form.subject,
        message: this.form.message,
      }),
    })

    if (response.ok) {
      this.showSuccess = true
      this.form = { name: '', email: '', subject: '', message: '' }
    } else {
      alert("Une erreur est survenue lors de l’envoi du message.")
    }
  } catch (error) {
    console.error("Erreur réseau :", error)
    alert("Impossible d’envoyer le message. Vérifiez votre connexion.")
  }

  this.isSubmitting = false

  // Masquer la notification après 4 secondes
  setTimeout(() => {
    this.showSuccess = false
  }, 4000)
}

  }
}
</script>

<style scoped>
