  display: flex;
  gap: 0.8rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem 1.2rem;
  background: #f8f9fa;
  border-radius: 10px;
  text-decoration: none;
  color: #6b7280;
  transition: all 0.3s ease;
  flex: 1;
  border: 1px solid #f3f4f6;
}

.social-link:hover {
  background: white;
  border-color: #e5e7eb;
